
.example button {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid #e2e2e2;
    border-radius: 3px;
    outline: none;
    background-color: white;
    padding: 5px 10px;
    font-size: 18px;
    cursor: pointer;
}
.example button:hover {
    border-color: #ccc;
    background-color: #f9f9f9;
}



